.page {
    display: flex;
    flex-direction: column;
    gap: 1.25rem;
}

/* Toolbar */
.toolbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    flex-wrap: wrap;
}
.navGroup {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}
.monthLabel {
    font-size: 1rem;
    font-weight: 600;
    color: var(--text-primary);
    min-width: 13rem;
    text-align: center;
    letter-spacing: -0.01em;
}
.navBtn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 2rem;
    height: 2rem;
    background: var(--bg-elevated);
    border: 1px solid var(--border);
    border-radius: var(--radius-md);
    color: var(--text-secondary);
    cursor: pointer;
    transition: all var(--transition);
    flex-shrink: 0;
}
.navBtn:hover {
    background: var(--bg-hover);
    color: var(--text-primary);
    border-color: var(--border-focus);
}

.rightGroup {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}
.viewGroup {
    display: flex;
    background: var(--bg-elevated);
    border: 1px solid var(--border);
    border-radius: var(--radius-md);
    overflow: hidden;
}
.viewBtn {
    padding: 0.375rem 0.875rem;
    background: none;
    border: none;
    border-right: 1px solid var(--border);
    color: var(--text-muted);
    font-size: 0.8125rem;
    font-weight: 500;
    cursor: pointer;
    transition: all var(--transition);
    white-space: nowrap;
}
.viewBtn:last-child {
    border-right: none;
}
.viewBtn:hover {
    color: var(--text-primary);
    background: var(--bg-hover);
}
.viewBtnActive {
    color: var(--purple-400);
    background: rgba(124, 58, 237, 0.15);
}
.todayBtn {
    padding: 0.375rem 0.875rem;
    background: var(--bg-elevated);
    border: 1px solid var(--border);
    border-radius: var(--radius-md);
    color: var(--text-secondary);
    font-size: 0.8125rem;
    font-weight: 500;
    cursor: pointer;
    transition: all var(--transition);
}
.todayBtn:hover {
    background: var(--bg-hover);
    color: var(--text-primary);
    border-color: var(--border-focus);
}

/* Layout */
.layout {
    display: flex;
    gap: 1rem;
    align-items: flex-start;
}
.calendarArea {
    flex: 1;
    min-width: 0;
    overflow: hidden;
}

/* Sidebar */
.sidebar {
    width: 188px;
    flex-shrink: 0;
    background: var(--bg-surface);
    border: 1px solid var(--border);
    border-radius: var(--radius-lg);
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
}
.sidebarTitle {
    font-size: 0.7rem;
    font-weight: 600;
    color: var(--text-muted);
    text-transform: uppercase;
    letter-spacing: 0.08em;
    margin-bottom: 0.375rem;
    margin-top: 0.25rem;
}
.sidebarTitle:first-child {
    margin-top: 0;
}
.calToggle {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.3rem 0;
    cursor: pointer;
    user-select: none;
}
.calToggle input[type="checkbox"] {
    display: none;
}
.checkBox {
    width: 13px;
    height: 13px;
    border-radius: 3px;
    border: 1.5px solid var(--border);
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all var(--transition);
}
.checkBoxMine {
    background: var(--purple-500);
    border-color: var(--purple-500);
}
.checkBoxFriend {
    background: var(--success);
    border-color: var(--success);
}
.calToggleName {
    font-size: 0.8rem;
    color: var(--text-muted);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    transition: color var(--transition);
}
.calToggleOn .calToggleName {
    color: var(--text-primary);
}
.calDivider {
    height: 1px;
    background: var(--border);
    margin: 0.375rem 0;
}

/* Month grid — fixed layout, never grows */
.monthGrid {
    background: var(--bg-surface);
    border: 1px solid var(--border);
    border-radius: var(--radius-lg);
    overflow: hidden;
}
.dayHeaders {
    display: grid;
    grid-template-columns: repeat(7, minmax(0, 1fr));
    border-bottom: 1px solid var(--border);
}
.dayHeader {
    padding: 0.5rem 0;
    text-align: center;
    font-size: 0.7rem;
    font-weight: 600;
    color: var(--text-muted);
    text-transform: uppercase;
    letter-spacing: 0.06em;
}
.monthCells {
    display: grid;
    grid-template-columns: repeat(7, minmax(0, 1fr));
}
.cell {
    /* Fixed height — NEVER aspect-ratio, that's what caused the bug */
    height: 6.5rem;
    padding: 0.375rem;
    border-right: 1px solid var(--border);
    border-bottom: 1px solid var(--border);
    overflow: hidden;
    cursor: default;
    transition: background var(--transition);
    min-width: 0;
}
.cell:nth-child(7n) {
    border-right: none;
}
.cell:hover {
    background: var(--bg-elevated);
}
.cellEmpty {
    background: transparent;
    cursor: default;
}
.cellToday .dayNum {
    background: var(--purple-500);
    color: #fff;
    border-radius: 50%;
}
.dayNum {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 1.5rem;
    height: 1.5rem;
    font-size: 0.775rem;
    font-weight: 500;
    color: var(--text-muted);
    margin-bottom: 0.2rem;
}
.pill {
    display: block;
    font-size: 0.68rem;
    padding: 0.1rem 0.35rem;
    border-radius: 3px;
    margin-bottom: 0.175rem;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
    font-weight: 500;
    transition: opacity var(--transition);
    line-height: 1.5;
}
.pill:hover {
    opacity: 0.75;
}
.pillMine {
    background: rgba(124, 58, 237, 0.22);
    color: var(--purple-300);
    border-left: 2px solid var(--purple-500);
}
.pillFriend {
    background: rgba(16, 185, 129, 0.18);
    color: #6ee7b7;
    border-left: 2px solid var(--success);
}
.overflow {
    font-size: 0.65rem;
    color: var(--text-muted);
    padding-left: 0.25rem;
    line-height: 1.4;
}

/* Week grid */
.weekGrid {
    background: var(--bg-surface);
    border: 1px solid var(--border);
    border-radius: var(--radius-lg);
    overflow: hidden;
    display: grid;
    grid-template-columns: repeat(7, minmax(0, 1fr));
}
.weekCol {
    border-right: 1px solid var(--border);
    min-width: 0;
}
.weekCol:last-child {
    border-right: none;
}
.weekColHeader {
    padding: 0.625rem 0.5rem;
    text-align: center;
    border-bottom: 1px solid var(--border);
}
.weekDay {
    font-size: 0.7rem;
    font-weight: 600;
    color: var(--text-muted);
    text-transform: uppercase;
    letter-spacing: 0.06em;
}
.weekDate {
    font-size: 1.1rem;
    font-weight: 600;
    color: var(--text-primary);
    line-height: 1.3;
    margin-top: 0.1rem;
}
.weekDateToday {
    color: var(--purple-400);
}
.weekColBody {
    padding: 0.5rem 0.375rem;
    min-height: 14rem;
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
}

/* Today view */
.todayView {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
}
.todayHeader {
    font-size: 0.78rem;
    font-weight: 600;
    color: var(--text-muted);
    text-transform: uppercase;
    letter-spacing: 0.08em;
}
.todayEvent {
    display: flex;
    gap: 0.875rem;
    padding: 0.875rem 1rem;
    background: var(--bg-surface);
    border: 1px solid var(--border);
    border-radius: var(--radius-md);
    cursor: pointer;
    transition: border-color var(--transition);
    align-items: flex-start;
}
.todayEvent:hover {
    border-color: var(--border-focus);
}
.todayTime {
    font-size: 0.775rem;
    color: var(--text-muted);
    width: 4.5rem;
    flex-shrink: 0;
    padding-top: 0.1rem;
    font-variant-numeric: tabular-nums;
}
.todayTitle {
    font-weight: 600;
    color: var(--text-primary);
    font-size: 0.9rem;
}
.todayCal {
    font-size: 0.775rem;
    color: var(--text-muted);
    margin-top: 0.2rem;
}
.todayStrip {
    width: 3px;
    border-radius: 2px;
    align-self: stretch;
    flex-shrink: 0;
}
.todayStripMine {
    background: var(--purple-500);
}
.todayStripFriend {
    background: var(--success);
}
.todayEmpty {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 3rem 1rem;
    gap: 0.625rem;
    color: var(--text-muted);
    text-align: center;
    background: var(--bg-surface);
    border: 1px solid var(--border);
    border-radius: var(--radius-lg);
}
.todayEmptyIcon {
    opacity: 0.2;
}

/* Event detail modal */
.overlay {
    position: fixed;
    inset: 0;
    background: rgba(0, 0, 0, 0.72);
    backdrop-filter: blur(6px);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 100;
    padding: 1rem;
    animation: fadein 120ms ease;
}
.modal {
    background: var(--bg-surface);
    border: 1px solid var(--border);
    border-radius: var(--radius-xl);
    padding: 1.75rem;
    width: 100%;
    max-width: 22rem;
    box-shadow:
        0 8px 40px rgba(0, 0, 0, 0.6),
        0 0 24px var(--purple-glow);
    animation: slideup 180ms cubic-bezier(0.34, 1.56, 0.64, 1);
}
.modalHeader {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 1.25rem;
    gap: 0.75rem;
}
.modalTitle {
    font-size: 1.1rem;
    font-weight: 700;
    color: var(--text-primary);
    line-height: 1.3;
}
.closeBtn {
    background: none;
    border: none;
    cursor: pointer;
    color: var(--text-muted);
    line-height: 0;
    padding: 0.25rem;
    border-radius: var(--radius-sm);
    transition: color var(--transition);
    flex-shrink: 0;
}
.closeBtn:hover {
    color: var(--text-primary);
}
.metaList {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
}
.metaRow {
    display: flex;
    gap: 0.625rem;
    font-size: 0.875rem;
    color: var(--text-secondary);
}
.metaIcon {
    flex-shrink: 0;
    margin-top: 0.15rem;
    color: var(--text-muted);
}
.metaLabel {
    font-size: 0.7rem;
    font-weight: 600;
    color: var(--text-muted);
    text-transform: uppercase;
    letter-spacing: 0.06em;
    margin-bottom: 0.2rem;
}
.calBadgeMine {
    display: inline-flex;
    padding: 0.2rem 0.6rem;
    border-radius: var(--radius-full);
    font-size: 0.7rem;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.04em;
    background: rgba(124, 58, 237, 0.2);
    color: var(--purple-300);
    border: 1px solid rgba(124, 58, 237, 0.3);
}
.calBadgeFriend {
    display: inline-flex;
    padding: 0.2rem 0.6rem;
    border-radius: var(--radius-full);
    font-size: 0.7rem;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.04em;
    background: var(--success-dim);
    color: var(--success);
    border: 1px solid rgba(16, 185, 129, 0.3);
}

.loading {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.75rem;
    padding: 4rem;
    color: var(--text-muted);
}
.spinner {
    width: 1.125rem;
    height: 1.125rem;
    border: 2px solid var(--border);
    border-top-color: var(--purple-400);
    border-radius: 50%;
    animation: spin 550ms linear infinite;
}
